import React from "react";

import './Projects.css'

// Slideshow Source: https://www.w3schools.com/howto/howto_js_slideshow.asp

// Include all projects here
const proj = [
    {
        title: "ChefUp",
        link: "https://github.com/amay723/ChefUp",
        icon: require('./icons/ChefUpLogo.png'),
        description: "Mobile application written in React Native",
        images: [
            "https://i.imgur.com/yQBKUSb.png",
            "https://i.imgur.com/hpnF4RH.png",
            "https://i.imgur.com/b11e1jF.png",
            "https://i.imgur.com/GnL4TOG.png",
            "https://i.imgur.com/cAnyBIs.png"
        ]

    },
    {
        title: "RideShare",
        link: "https://github.com/amay723/RideShare",
        icon: require('./icons/RideShareLogo.png'),
        description: "Entry for the SSU 2019 Hachathon"
    }
];

class Projects extends React.Component {

    constructor(props) {
        super(props);

        this.projPicIndices = {};

    }

    componentDidMount() {
        for( let elem in this.projPicIndices ) {
            this.showSlides(elem);
        }
    }

    // Next/previous controls
    plusSlides(projN, n) {

        console.log(n);
        console.log(this.projPicIndices);

        this.projPicIndices[projN] += n;

        this.showSlides(projN);
    }

    showSlides(n) {
        let i;

        let classN = "project" + n;

        let slides = document.getElementsByClassName(classN);
        // var dots = document.getElementsByClassName("dot");
        if (this.projPicIndices[n] > slides.length) {this.projPicIndices[n] = 1}
        if (this.projPicIndices[n] < 1) {this.projPicIndices[n] = slides.length}
        for (i = 0; i < slides.length; i++) {
            slides[i].style.display = "none";
        }
        // for (i = 0; i < dots.length; i++) {
        //     dots[i].className = dots[i].className.replace(" active", "");
        // }
        slides[this.projPicIndices[n]-1].style.display = "block";
        // dots[slideIndex-1].className += " active";
    }


    render() {

        document.title = 'Projects';


        return (
            <div className="projects-container">
                <h3>My Projects (WIP)</h3>

                {
                    proj.map((itm, projIdx) => {
                        return (<div className="App-projects">
                            <h4 style={{color: 'brown'}}>{itm.title}</h4>
                            <img alt="Project Logo" src={itm.icon}/>
                            <a target="_blank" rel="noopener noreferrer" href={itm.link}>
                                <svg className="octicon octicon-mark-github v-align-middle" height="32"
                                     viewBox="0 0 16 16" version="1.1" width="32"
                                     aria-hidden="true">
                                    <path
                                        d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0 0 16 8c0-4.42-3.58-8-8-8z"/>
                                </svg>
                            </a>
                            <p>{itm.description}</p>

                            <br />

                            {/* If project has images, display slideshow */}
                            {itm.images &&
                            (<div className="slideshow-container">
                                {
                                    itm.images.map((url, picIdx) => {

                                        // Initialize current selected image
                                        if( this.projPicIndices[projIdx] === undefined )
                                            this.projPicIndices[projIdx] = 1;

                                        let projNum = "project" + projIdx;
                                        let classN = "mySlides fade " + projNum;

                                        return (
                                            <div className={classN}>
                                                <div className="numbertext">{picIdx + 1} / {itm.images.length}</div>
                                                <img className="images" src={itm.images[picIdx]} alt="project example"/>
                                                {/*<div className="text">Caption Text</div>*/}
                                            </div>
                                        );
                                    })
                                }

                                {/*Next and previous buttons*/}
                                <button className="prev" onClick={() => this.plusSlides(projIdx, -1)}>&#10094;</button>
                                <button className="next" onClick={() => this.plusSlides(projIdx, 1)}>&#10095;</button>

                            </div>)
                            }

                        </div>)
                    })
                }
            </div>
        );
    }
}

export default Projects;